body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: #AAA;
}

ol, ul {
  padding-left: 30px;
}

.game-info {
  margin-left: 20px;
}

table {
  background-color: #ddd;
  border-collapse: separate;
  border-spacing: 0px;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

td {
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  position: relative;
}

.status {
  margin-bottom: 10px;
}

/* cell and header formats */
.row-head {
  border-bottom: 1px solid #999;
  font-size: 18px;
  font-weight: bold;
  padding-left: 1vh;
  padding-right: 1vh;
  text-align: right;
}

.col-head {
  border-left: 1px solid #999;
  font-size: 18px;
  font-weight: bold;
  padding-top: 1vh;
  padding-bottom: 1vh;
  text-align: right;
  text-orientation: mixed;
  writing-mode: vertical-rl;
}

.square {
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 44px;
  height: 44px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 44px;
}

/* put a line after every 5th row / col */
tr:nth-child(5n) td {
  border-bottom-color: red;
  border-bottom-width: 2px;
  border-bottom-style: solid;
}

td:nth-child(5n + 1) {
  border-right-color: red;
  border-right-width: 2px;
  border-right-style: solid;
}

/* Highlighting the active row + col */
tr:hover {
  background-color: aquamarine;
}

thead > tr:hover {
  background-color: unset;
}

td:hover::after { 
    background-color: aquamarine;
    content: '\00a0';  
    height: 10000px;    
    left: 0;
    position: absolute;  
    top: -5000px;
    width: 100%;
    z-index: -1;        
}

.dark {
  background: #111;
}

.debug-text {
  overflow: hidden;
  width: 10fr;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

